import { FC } from "react";
import { Link, useParams } from "react-router-dom";
import { IArticle, IArticleComponent, ILibrarySection } from "../types/library";
interface IPage {
  library: ILibrarySection[];
}
export const Article: FC<IPage> = ({ library }) => {
  const { path }: any = useParams();
  let currentArticle: IArticle = {
    articleTitle: "",
    titleImg: {
      width: 0,
      height: 0,
      src: "",
      alt: "",
    },
    path: "",
    articleComponents: [],
  };
  let nextArticlePath: string = "";
  let currentArticlePath: string = "";

  const getArticle = () => {
    library.map((section: ILibrarySection, indexSection: number) => {
      section.articles.map((article: IArticle, indexArticle: number) => {
        if (article.path === path) {
          currentArticle = article;
          indexArticle !== section.articles.length - 1
            ? (nextArticlePath = getNextArticlePath(
                indexSection,
                indexArticle + 1
              ))
            : (nextArticlePath = "");
          currentArticlePath = getCurrentPath(indexSection, indexArticle);
        }
      });
    });
  };
  const getNextArticlePath = (
    currentSection: number,
    nextArticleIndex: number
  ) => {
    return library[currentSection].articles[nextArticleIndex].path;
  };
  const getCurrentPath = (currentSection: number, nextArticleIndex: number) => {
    let lib = "Library";
    let sectionName = library[currentSection].sectionTitle;
    let articleName =
      library[currentSection].articles[nextArticleIndex].articleTitle;
    console.log(`${lib + " -> " + sectionName + " -> " + articleName}`);
    return `${lib + " -> " + sectionName + " -> " + articleName}`;
  };
  getArticle();
  return (
    <article className="article">
      <nav className="article-nav article-nav_top">
        <Link to={"/library"}>
          <div className="article-nav__link">{"<< BACK TO THE LIBRARY"}</div>
        </Link>
        <Link to={`/library/${nextArticlePath}`}>
          <div className="article-nav__link">{"NEXT TOPIC >>"}</div>
        </Link>
      </nav>
      <div className="article__path">{currentArticlePath}</div>

      <h2 className="article__title">{currentArticle.articleTitle}</h2>

      <div className="article__main">
        <img
          className="article__title-img"
          src={currentArticle.titleImg?.src}
          alt={currentArticle.titleImg?.alt}
          width={currentArticle.titleImg?.width}
          height={currentArticle.titleImg?.height}
        />
        {currentArticle.articleComponents.map(
          (component: IArticleComponent, index: number) => (
            <section key={index} className="section">
              {component.title && (
                <h4 className="section__title">{component.title}</h4>
              )}
              {component.subtitle && (
                <h5 className="section__title">{component.subtitle}</h5>
              )}
              {component.img && (
                <div className="article__img-block">
                  <div className="article__img-description">
                    {component.img?.description}
                  </div>
                  <img
                    className="article__img"
                    src={component.img?.src}
                    alt={component.img?.alt}
                    width={component.img?.width}
                    height={component.img?.height}
                  />
                </div>
              )}
              {component.text && (
                <p className="section__text">{component.text}</p>
              )}
              {component.list && (
                <ul className="section__list">
                  {component.list.map((listItem: string, index: number) => (
                    <li key={index}>{listItem}</li>
                  ))}
                </ul>
              )}
            </section>
          )
        )}
      </div>
      <nav className="article-nav article-nav_bottom">
        <Link to={"/library"}>
          <div className="article-nav__link">{"<< BACK TO THE LIBRARY"}</div>
        </Link>
        <Link to={`/library/${nextArticlePath}`}>
          <div className="article-nav__link">{"NEXT TOPIC >>"}</div>
        </Link>
      </nav>
    </article>
  );
};
