export const Map = () => {
  return (
    <div className="library">
      <h2>NAVIGATION</h2>
      <div className="library__main">
        <p>
          When creating the learning platform, special attention was paid to
          navigation, the main task was to make access to the page with
          educational information as fast as possible. The construction of the
          navigation system is based on the rule of three clicks - it assumes
          that the user should be able to find any information in no more than 3
          mouse clicks. According to this rule, the project has the following
          structuring:
        </p>
        <ul className="ul">
          <li>1st level - Main page</li>
          <li>
            <ul>
              <li>2nd level - Thematic section</li>
              <li>
                <ul>3rd level - Thematic page</ul>
              </li>
            </ul>
          </li>
        </ul>
        <p>
          The hierarchy can be represented schematically as follows picture:
        </p>
        <img width={1040} height={693} src="img/pages/map/1.png" alt="" />
        <p>
          On the main page (Main screen) there is a menu with which you can
          navigate through the thematic sections of the site and select the
          language. Pages that contain the main and / or most significant
          sections for the learning platform are placed in a separate menu
          below, provided with illustrations.
        </p>
        <p>
          The second level of the hierarchy is the thematic section. The
          "Library" section contains articles divided into topics, and topics
          into subtopics, which contain the names of articles and links to them.
          In the "Tests" section there are tests on all topics.
        </p>
        <p>
          The third level of the hierarchy is an article with educational
          information, also The navigation part of the 2nd level page consists
          of top and bottom navigation panels. When building navigation
          elements, the principle of duplicating transitions is used, namely, at
          the top and bottom of the article there is a link to the next article
          and a link to the "Library", if the article is the last one in topic
          or subtopic, a link to the next article returns to the "Library",
          which allows you to provide access to any piece of information in at
          least two ways. The top navigation bar also contains a full path from
          the main page to the current page.
        </p>
        <p>
          The article has a main title, which duplicates the name of the
          thematic section in the "Library", and is the title of the article,
          for example HISTORY OF STM32. A heading that characterizes the
          information stated in the "subsection" of the article, for example
          "HARDWARE". The subheading is used to logically separate the text, for
          example "STEP 1". There are also lists with the help of which
          sequential information is submitted, or an enumeration. Illustrations
          and tables are signed at the top in bold type, for example Tera Term
          output. P.S. if it fails, just reload the page
        </p>
      </div>
    </div>
  );
};
