import { FC } from "react";
import { ITest } from "../types/types";
interface ITests {
  tests: ITest[];
}
export const Tests: FC<ITests> = ({ tests }) => {
  function checkAnswer(e: any, test: ITest) {
	  if (!test.answers[e.target.id]){
		return
	  }
    if (true === test.answers[e.target.id].bool) {
      e.target.classList.add("answer_true");
    } else {
      e.target.classList.add("answer_false");
    }
    console.log();
  }

  return (
    <div className="library">
      <h2>Tests</h2>

      <div className="library__main">
        {tests.map((test) => (
          <>
            <h5 className="question">{test.question}</h5>

            {test.answers.map((answer: any, index: number) => (
              <button
                onClick={(e) => {
                
                  checkAnswer(e, test);
                }}
                id={String(index)}
                className="answer"
              >
                {answer.answer}
              </button>
            ))}
          </>
        ))}
      </div>
    </div>
  );
};
