import { FC } from "react";
import { IMenu, VariantsOfMenu } from "../types/types";
import { MenuItem } from "../components/MenuItem";

interface IMain {
  menuItems: IMenu[];
}
export const Main: FC<IMain> = ({ menuItems }) => {
  return (
    <main className="main">
      <div className="main__header">
        <h2 className="main__headline">getting  started with STM32 </h2>
        <p className="main__text">
          STM32 is a family of 32-bit microcontroller integrated circuits by
          STMicroelectronics. The STM32 chips are grouped into related series
          that are based around the same 32-bit ARM processor core, such as the
          Cortex-M33F, Cortex-M7F, Cortex-M4F, Cortex-M3, Cortex-M0+, or
          Cortex-M0.
        </p>
      </div>
      <nav className="main-menu">
			
        {menuItems.map((item, index) =>
          index !== 0 ? (
            <MenuItem
              variant={VariantsOfMenu.main}
              key={index}
              menuItem={item}
            />
          ) : null
        )}
      </nav>
    </main>
  );
};
