import  { FC } from "react";
import {  NavLink } from "react-router-dom";
import { IMenu, VariantsOfMenu } from "../types/types";
interface IItem {
  variant: VariantsOfMenu.header | VariantsOfMenu.main;
  menuItem: IMenu;
}
export const MenuItem: FC<IItem> = ({ menuItem, variant }) => {
  return (
    <>
      <NavLink exact={true} to={menuItem.link}>
        {variant === VariantsOfMenu.header ? (
          <li className={`header-menu__item`}>{menuItem.headline}</li>
        ) : null}
        {variant === VariantsOfMenu.main ? (
          <div className="main-menu__item">
            <h3 className="main-menu__headline">{menuItem.headline}</h3>
            <img
              src={menuItem.src}
              width="337"
              height="422"
              className="main-menu__img"
            />
          </div>
        ) : null}
      </NavLink>
    </>
  );
};
