import React, { FC } from "react";
import { Link } from "react-router-dom";
import { IArticle, ILibrarySection } from "../types/library";
interface ILibraryInfoSection {
  section: ILibrarySection;
}
export const LibraryInfoSection: FC<ILibraryInfoSection> = ({ section }) => {
  return (
    <>
      {section.gigatitle ? (
        <h4 className="library__giga">{section.gigatitle}</h4>
      ) : null}
      <ul className="library-section">
        <li className="library-section__headline">{section.sectionTitle}</li>
        {section.articles.map((article: IArticle, index: number) => (
          <Link key={index} to={`/library/${article.path}`}>
            <li className="library-section__link"> {article.articleTitle}</li>
          </Link>
        ))}
      </ul>
    </>
  );
};
