import  { useContext } from "react";
import { AppContext } from "../App";
import { LibraryInfoSection } from "../components/LibrarySection";
import { ILibrarySection } from "../types/library";

export const Library = () => {
  const { library } = useContext(AppContext);

  return (
    <>
      <section className="library">
        <h2>LIBRARY</h2>
        <div className="library__main">
          <h4>General Information ABOUT STM32</h4>
          <nav className="library__sections">
            {library.map((section: ILibrarySection) => (
              <LibraryInfoSection key={section.id} section={section} />
            ))}
          </nav>
        </div>
      </section>
    </>
  );
};
