import { FC, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { IMenu, VariantsOfMenu } from "../types/types";
import { MenuItem } from "./MenuItem";
interface IHeader {
  menuItems: IMenu[];
}
export const Header: FC<IHeader> = ({ menuItems }) => {
  useEffect(() => {
    document.querySelector(".language")?.addEventListener("click", (e) => {
      document.querySelector(".language__menu")?.classList.toggle("_active");

      console.log();
    });
    document.querySelector(".burger")?.addEventListener("click", (e) => {
      document.querySelector(".header-menu")?.classList.toggle("cheked");
      document.querySelector(".burger")?.classList.toggle("burger_cheked");
      document.querySelector("body")?.classList.toggle("lock");
      console.log();
    });
  }, []);

  return (
    <header className="header">
      <Link to="/">
        <img src="img/LOGO.png" width="127" height="23" className="logo" />
      </Link>

      <nav className="header-menu">
        <ul className="header-menu__container">
          {menuItems.map((item, index) => (
            <MenuItem
              variant={VariantsOfMenu.header}
              key={index}
              menuItem={item}
            />
          ))}
          <Link className="navigation" to="/map">
            <div style={{ marginRight: 25, cursor: "pointer" }}>NAVIGATION</div>
          </Link>
          <div className="language">
            SELECT LANGUAGE
            <ul className="language__menu">
              <li
                onClick={() => localStorage.setItem("lang", "en")}
                className="language__item"
                data-google-lang="en"
              >
                EN
              </li>
              <li
                onClick={() => localStorage.setItem("lang", "ua")}
                className="language__item"
                data-google-lang="uk"
              >
                UA
              </li>
            </ul>
          </div>
        </ul>
      </nav>
      <div className="burger">
        <span></span>
      </div>
    </header>
  );
};
