import "./styles/style.scss";
import { Header } from "./components/Header";
import { Main } from "./pages/Main";
import { Route } from "react-router-dom";
import { Library } from "./pages/Library";
import { createContext, useEffect, useState } from "react";
import { Article } from "./pages/Article";
import { Map } from "./pages/Map";
import { Switch } from "react-router-dom";
import { library, menuItems, tests, libraryUA } from "./store/store";
import { Tests } from "./pages/Tests";
export const AppContext: any = createContext({});
function App() {
  

  return (
    <AppContext.Provider value={{ library: library, menuItems: menuItems }}>
      <div className="App">
        <div className="wrapper">
          <Header menuItems={menuItems} />
          <Switch>
            <Route path="/" exact>
              <Main menuItems={menuItems} />
            </Route>
            <Route exact path="/library">
              <Library />
            </Route>
            <Route exact path="/library/:path">
              <Article library={localStorage.getItem('lang') === "ua" ? libraryUA : library} />
            </Route>
            <Route exact path="/tests">
              <Tests tests={tests} />
            </Route>
            <Route exact path="/map">
              <Map />
            </Route>
          </Switch>
        </div>
      </div>
    </AppContext.Provider>
  );
}

export default App;
